a.button {
  padding-top: 10px; }

.button {
  background-color: #8BC34A;
  border: none;
  color: #fff;
  height: 50px;
  width: 100%;
  max-width: 300px;
  font-weight: 500;
  font-size: 18px;
  margin: 20px auto;
  -webkit-box-shadow: -1px 6px 7px -2px #bbb;
          box-shadow: -1px 6px 7px -2px #bbb;
  display: block;
  position: relative;
  text-decoration: none;
  text-align: center; }
  .button .fa {
    position: absolute;
    left: 15px;
    bottom: 7px; }
  .button.button--white {
    border: 1px solid #FF4081;
    color: #093B88;
    background: #fff; }
  .button.button--blue {
    color: #fff;
    background: #093B88; }
  .button.button--facebook {
    background-color: #3B5998; }
  .button.button--twitter {
    background-color: #00ACED; }
  .button.button--google {
    background-color: #EA4335; }
  .button.button--slim {
    height: 30px; }
  .button.button--dark-shadow {
    -webkit-box-shadow: -1px 6px 7px -2px #333;
            box-shadow: -1px 6px 7px -2px #333; }

.button:disabled {
  opacity: 0.5; }



