.panel {
  background: #fff;
  display: block;
  width: 33.3333333333%;
  padding: 20px 5%;
  min-height: calc( 100vh - 55px); }
  .panel.panel--clear {
    background: none; }
  .panel.panel--left {
    border-right: 2px solid #093B88; }
  .panel.panel--right {
    border-left: 2px solid #093B88; }
  .panel.panel--double {
    width: 66.6666666666%; }
  .panel.panel--internal {
    width: 50%;
    border: none;
    padding: 0 5%;
    height: 100%;
    background: none; }



